.movies .movie {
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  background-color: #ffffff;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  border-radius: 5px;
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
  font-weight: 500;
}

.movie .genres {
  display: flex;
  justify-content: flex-start;
  padding: 0px;
}
.movie .genres__genre {
  opacity: 0.6;
  margin-right: 10px;
  font-size: 15px;
}

.movie .movie__year {
  margin-top: 10px;
  font-size: 16px;
  font-weight: 400;
}

.container {
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  color: white;
  font-size: 30px;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(100px, 1fr));
  grid-gap: 100px;
  padding: 10px;
  width: 80%;
  padding-top: 70px;
}

@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
  }
}

.genres {
  list-style: none;
}

.movie .movie__summary {
  opacity: 0.4;
  font-size: 13px;
}

.movie .movie__rating {
  display: flex;
  margin-top: 10px;
  font-size: 14px;
  align-items: center;
}

.movie .fa-star {
  font-size: 11px;
  margin: 0px 4px;
  color: #f1c40f;
}
